.toggleContainer {
  position: fixed;
  bottom: 10px;
  left: 20px;
  display: flex;
  justify-content: center;
  z-index: 100;

  .label {
    width: 40px;
    height: 34px;
    display: flex;
    align-items: center;
    margin: 0 4px;
    color: darkgray;
  }

  /* The switch - the box around the slider */
  .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;

    /* Hide default HTML checkbox */
    input {
      opacity: 0;
      width: 0;
      height: 0;
    }
  }

  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    &:before {
      position: absolute;
      content: '';
      height: 26px;
      width: 26px;
      left: 4px;
      bottom: 4px;
      background-color: white;
      -webkit-transition: 0.4s;
      transition: 0.4s;
    }
  }

  input:checked + .slider {
    background-color: #2196f3;
    &:before {
      -webkit-transform: translateX(26px);
      -ms-transform: translateX(26px);
      transform: translateX(26px);
    }
  }

  input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
  }

  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
    &:before {
      border-radius: 50%;
    }
  }
}

@media (max-width: 768px) {
  .toggleContainer {
    bottom: unset;
    top: 10px;
  }
}